#mainProjectContainer{
    margin-top: 100px;
    background-color: black;
}

#mainProjectContainer nav{
    color: white;
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    margin-bottom: 100px;
}

#nav-links{
    display: flex;
    justify-content: center;
}

#nav-links a{
    color: white;
    text-decoration: none;
    letter-spacing: 3px;
    font-weight: bold;
    font-family: 'Roboto Mono', monospace;
}

#nav-links li{
    padding-right: 30px;
    list-style: none;
}

li a:hover{
    background-color: turquoise;
    transition:all 0.5s ease;
}

#mainProjectContainer h1{
    color: rgb(236, 240, 13);
    font-size: 2em;
    font-family: 'Roboto Mono', monospace;

}

#pText{
    color: white;
    width: 600px;
    text-align: left;
    padding-left: 10%;
    font-size: 1.5em;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    font-family: 'Roboto Mono', monospace;

}

#mainProjectContainer img{
    height: 400px;
    width: 600px;
    border-radius: 25%;
    margin-left: 100px;
    margin-top: 40px;
}

#mainProjectContainer img:hover{
    border: solid 4px rgb(236, 240, 13);
    
}

#mainProjectContainer div{
    display: flex;
    flex-direction: row;
    /* padding-bottom: 50px; */
}